import React, { useContext, useEffect, useState } from 'react'
import style from "./Movies.module.css"
import { MoviesContext } from '../../MoviesContext';
import Fade from 'react-reveal/Fade';
import { NavLink } from 'react-router-dom'
 
export default function Movies() {
    const { searchMovies, imgPrefix } = useContext(MoviesContext)
    let [searchQuery, setSearchQuery] = useState("");
    const [searchMovies_Array, setSearchMovies_Array] = useState([]);
    // function get_SearchMovies(searchQuery) {
    //     // console.log(searchQuery);
    //     searchMovies(searchQuery);
    //     console.log(searchMovies_Array);
    // }
    useEffect(async () => {
        let x = [];
        if (searchQuery !== '')
            x = await searchMovies(searchQuery);
        setSearchMovies_Array(x);
    }, [searchQuery]);
 
    return (
        <div className={`container-lg  container-fluid`}>
            <div>
                <h1 className={`${style.vh_10} ${style.readyText} py-2 text-center position-relative `} >Ready to watch ? Start Browsing</h1>
                <input onChange={(e) => setSearchQuery(e.target.value)} id={style.searchInput} type="text" placeholder="You can search for it here !" className="text-center form-control" />
            </div>
            <div className={` bg-danger my-4  ${style.bg_lniear} ${searchQuery?"p-3":""} `}>
                <div className="row ">
                    {
                        searchMovies_Array.map((movie, index) => <div className="col-md-3"> <NavLink to={`/mediaDetails/movie/${movie.id}`} className="movie">
                            <img className='w-100' src={imgPrefix + movie.poster_path} alt={movie.title} />
                            <h3 className='h6 p-2 bg-dark text-center'>{movie.original_title}</h3>
                        </NavLink> </div>)
                    }

                </div>
            </div>
            {/* end of search Div  */}
            <div className=""> 
            <nav class={`navbar navbar-expand-lg navbar-dark ${style.moviesCategory}`}>
  <div class="container-fluid">
    <a class="navbar-brand" href="#">Movies</a>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav me-auto mb-2 mb-lg-0 d-flex justify-content-around">
        <li class="nav-item">
          <button class="nav-link active border-0 bg-transparent"  >Latest</button>
        </li>
        <li class="nav-item">
          <button class="nav-link border-0 bg-transparent " >Now Playing</button>
        </li>
        <li class="nav-item">
          <button class="nav-link border-0 bg-transparent "   >Popular</button>
        </li>
        <li class="nav-item">
          <button class="nav-link border-0 bg-transparent "  >Top Rated</button>
        </li>
        <li class="nav-item">
          <button class="nav-link border-0 bg-transparent"   >Upcoming</button>
        </li>
      
     
  
     
      </ul>
      
    </div>
  </div>
</nav>
            </div>

        </div>
    )
}
